/* From Uiverse.io by Fernando-sv */ 
.loader {
    border: 4px solid rgba(0, 0, 0, .5);
    border-left-color: transparent;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 45%;

  }
  
  .loader {
    border: 4px solid rgba(0, 0, 0, .5);
    border-left-color: transparent;
    width: 36px;
    height: 36px;
  }
  
  .loader {
    border: 4px solid rgba(0, 0, 0, .5);
    border-left-color: transparent;
    width: 36px;
    height: 36px;
    animation: spin89345 1s linear infinite;
  }
  
  @keyframes spin89345 {
    0% {
      transform: rotate(0deg);
    }
  
    100% {
      transform: rotate(360deg);
    }
  }